<script>
import AxiosHelper from './components/AxiosHelper.vue';
import PopupTrigger from './components/PopupTrigger.vue';

export default {
  name: 'App',
  components: {PopupTrigger},
  mixins: [AxiosHelper],

  data: () => ({
    lightTheme: false,

    routePath: '',
    userId: null,
    activeComponents: []
  }),

  watch: {
    lightTheme: function(light) {
      this.$vuetify.theme.dark = !light;
    },

    $route (to, from){
      this.routePath = to.path;
    }
  },

  methods: {
    isRouteButtonVisible(routeStart, activeComponent) {
      return this.routePath.startsWith(routeStart)
          || this.activeComponents.includes(activeComponent);
    },

    setUserInfo(userInfo) {
      this.userId = userInfo.userId;
      this.activeComponents = userInfo.activeComponents;
    }
  },

  mounted () {
    this.credentialedAxios('fetching active components', axios => axios.get(this.$apiUrl + '/userInfo'),
      data => this.setUserInfo(data));
  }
};
</script>
